document.addEventListener('DOMContentLoaded', function(){
    iniciarApp();
})

function iniciarApp() {
    crearGaleria();
    scrollNav();
    navegacionFija();
}

//para avanzar en la pagina dependiendo de la navegacion principal
function scrollNav() {
    const enlaces =document.querySelectorAll('.navegacion-principal a')
    enlaces.forEach(enlace =>{
        enlace.addEventListener('click', function name(e) {
            e.preventDefault();

            const seccion =document.querySelector(e.target.attributes.href.value);
            seccion.scrollIntoView({behavior:'smooth'});
        })
    })
}
//BARRA DE NAVEGACION FIJA
function navegacionFija() {
    const barra = document.querySelector('.header');
    const sobreFestival = document.querySelector('.sobre-festival');

    window.addEventListener('scroll', function () {
        console.log(sobreFestival.getBoundingClientRect());
        if(sobreFestival.getBoundingClientRect().top < 0){
            barra.classList.add('fijo');
            body.classList.add('body-scroll');
            // console.log('ya pasaste brou');
        }else{
            barra.classList.remove('fijo');
            body.classList.remove('body-scroll');
            // console.log('aun nel');
        }
    })
}

//CREAMOS LA GALERIA 
function crearGaleria() {
    const galeria = document.querySelector('.galeria-imagenes');
    // galeria.textContent = 'vamos a crear una galeria';
    for(let i=1; i<=12;i++){
        const imagen= document.createElement('picture');
        imagen.innerHTML = `
            <source srcset="build/img/thumb/${i}.avif" type="Imagen  avif">
            <source srcset="build/img/thumb/${i}.webp" alt="Imagen  webp">
            <img loading="lazy" src="build/img/thumb/${i}.jpg"></img>

            `;
            //detecta clic en imagen
            imagen.onclick= function(){
                mostrarImagen(i);
            }
        galeria.appendChild(imagen);
    }
}
function mostrarImagen(img) {
    console.log('mostranto..', img);
    const imagen= document.createElement('picture');
        imagen.innerHTML = `
            <source srcset="build/img/grande/${img}.avif" type="Imagen  avif">
            <source srcset="build/img/grande/${img}.webp" alt="Imagen  webp">
            <img loading="lazy" src="build/img/grande/${img}.jpg"></img>

            `;
    //crea overlay con la imagen
    const overlay = document.createElement('DIV');
    overlay.appendChild(imagen);
    overlay.classList.add('overlay');
    overlay.onclick = function(){
        const body = document.querySelector('body');
        body.classList.remove('fijar-body');//se quita tambien el no poder dar scroll
        overlay.remove();
    }

    //Boton para cerrar imagen
    const cerrarModal = document.createElement('P');
    cerrarModal.textContent='X'; 
    cerrarModal.classList.add('btn-cerrar');
    cerrarModal.onclick=function () {
        const body = document.querySelector('body');
        body.classList.remove('fijar-body');//se quita tambien el no poder dar scroll
        overlay.remove();
    }
    overlay.appendChild(cerrarModal);
    // a√±adirlo a html
    const body = document.querySelector('body');
    body.appendChild(overlay);
    body.classList.add('fijar-body');//se quita el dar scroll

}